html {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

body {
  color: #333;
  margin: 0 auto;
  padding: 0;
  background: #f8f8f8;
  font-family: proxima-nova, Helvetica Neue, Helvetica, Arial, sans-serif;
  text-align: center;
}

li {
  list-style: none;
}

.App {
  text-align: center;
  max-height: 80%;
}

.App-header {
  background-color: white;
  color: #444;
  border-bottom: 1px solid #111;
  display: block;
  height: 70px;
  position: fixed;
  width: 90%;
  margin: auto;
  padding-top: 30px;
}

.App-title {
  font-size: 1em;
}

.App-container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-bottom: 20px;
  padding-top: 18px;
  padding-left: 24px;
  padding-right: 24px;
  margin-left: auto;
  margin-right: auto;
  max-width: 800px;
}



.price {
  color: #00ace6;
  text-align: center !important;
}

/***** Home Image *****/
/* Container holding the image and the text */
.container {
  position: relative;
  text-align: center;
  color: white;
  margin: 10px auto;
  max-width: 100%;
}

.jacket {
    position: absolute;
    top: 14%;
    left: 40%;
}

.blouse {
    position: absolute;
    top: 33%;
    left: 55%;
}

.pants {
    position: absolute;
    top: 65%;
    left: 65%;
}

.shoes {
    position: absolute;
    bottom: 30%;
    left: 30%;
}

.circle:hover {
  opacity: 1;
}

.circle {
  background-color: red;
  height: 25px;
  width: 25px;
  border-radius: 50%;
  opacity: 0.5;
}

/***** MODAL *****/
.modal-container {
  display: block;
  height: 100%;
  left: 5%;
  position: fixed;
  /* top: 0; */
  width: 90%;
}

.modal-header,
.modal-footer {
    height: 70px;
    background-color: white;
    overflow: hidden;
    padding-left: 5%;
    padding-right: 5%;
}

.modal-header-row-2 {
  text-align: left;
}

.modal-header-row-2 span {
  float: right;
}

.modal-body,
.modal-footer {
    position: absolute;
    width: 100%;
}

.modal-body {
    bottom: 50px;
    top: 70px;
    overflow-y: auto;
    background-color: white;
    padding: 0 5%;
}

#piece {
    max-width: 100%;
    display:block;
    margin:auto;
}

#message {
  margin: 3% auto;
  text-align: left;
  max-width: 70%;
}

#message span {
  text-align: left;
}

.description-title {
  text-align: left;
}

.copy {
  margin: auto;
}

.copy p {
  margin: 1em 0;
  display: block;
  letter-spacing: 0.5px;
  line-height: 1.85;
  font-size: 14px;
  padding-bottom: 8px;
  text-align: left;
}

.modal-footer {
    bottom: 0;
    background-color: #111;
    color: #ccc;
    text-align: center;
    font-size: 1.3em;
    height: 50px;
}

.modal-footer:hover {
  background-color: #333;
  color: #eee;
}

.animate {
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  -o-transition: transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
  -webkit-box-shadow: none;
          box-shadow: none;
}

.inactive {
  -webkit-transform: none !important;
      -ms-transform: none !important;
          transform: none !important;
  -webkit-box-shadow: none;
          box-shadow: none;
}

/*********** comment box ***********/
.callout.secondary {
  margin-top: 20px;
  background: #222;
  color: white;
}

.comment-list {
  list-style: none;
  margin: 0 0 50px 0;
  > li {
    margin: 0 0 20px 0;
    background: #333;
    padding: 20px;
  }
}
.comment-user {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  margin: 0 0 10px 0;
}
.comment-user-avatar-wrap {
  width: 40px;
  margin-right: 10px;
}
.comment-username {
  margin-right: 5px;
  color: white;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.on {
  margin-right: 5px;
}
.comment-text {
  background: white;
  color: #222;
  padding: 20px;
  :last-child {
    margin: 0;
  }
}



